import React,{ useEffect, useState } from 'react';
import TinderCard from 'react-tinder-card';
import "./TinderCards.css";
import axios from './axios';

function TinderCards() {
  const [people, setPeople]=useState([
    {
      name:"Elon Musk",
      imgUrl:"https://ichef.bbci.co.uk/news/976/cpsprodpb/08CB/production/_127815220_musk-index-getty.jpg"
    },
    {
      name:"Teresa Palmer",
      imgUrl :"https://c4.wallpaperflare.com/wallpaper/806/916/60/teresa-palmer-4k-best-high-resolution-wallpaper-preview.jpg"
    },
    {
      name: "Thor",
      imgUrl: "https://cdn.murphysmultiverse.com/wp-content/uploads/2021/12/hemsworth_720.jpg"
    }
  ]);
  useEffect(()=>{
    async function fetchData(){
        const req= await axios.get('/tinder/cards');
        setPeople(req.data);
    }
    fetchData();
  },[]);
  
  console.log(people);


  const swiped=(direction, nameToDelete)=>{
    console.log("removing"+nameToDelete);
    //setLastDirection(direction);
  }
  const outOfFrame=(name)=>{
    console.log(name+" left the screen");
  }
  return (
    <div className='tindercards'>
      <div className='tindercards_cardcontainer'>
        {people.map((person)=>(
          <TinderCard
            className="swipe" 
            key={person.name} 
            preventSwipe={["up","down"]}
            onSwipe={(dir)=>swiped(dir,person.name)}
            onCardLeftScreen={()=>outOfFrame(person.name)}
          >
            <div 
              style={{ backgroundImage: `url(${person.imgUrl})`}}
              className="card"
            >
              <h3>{person.name}</h3>
            </div>
          </TinderCard>
        ))}
      </div>
    </div>
  )
}

export default TinderCards